import React from 'react';
import { Line } from 'react-chartjs-2';
import { Chart } from 'react-chartjs-2';
import { defaults } from 'react-chartjs-2';

// Mock dataset, later word dit ingeladen via de nodejs api
const d1 = {
  labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
  datasets: [
    {
      label: 'Coca-Cola Zero',
      fill: false,
      lineTension: 0.1,
      backgroundColor: 'rgba(75,192,192,0.4)',
      borderColor: 'rgba(75,192,192,1)',
      borderCapStyle: 'butt',
      borderDash: [],
      borderDashOffset: 0.0,
      borderJoinStyle: 'miter',
      pointBorderColor: 'rgba(75,192,192,1)',
      pointBackgroundColor: '#fff',
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
      pointHoverBorderColor: 'rgba(220,220,220,1)',
      pointHoverBorderWidth: 2,
      pointRadius: 1,
      pointHitRadius: 10,
      data: [65, 59, 80, 81, 56, 55, 40]
    }
  ]
};

// Genereert de image
function done() {
    var url=this.chart.toBase64Image();
    document.getElementById("graph").src=url;
}
// Roept methode done aan zodra de animatie compleet is. Options is ook om grafiekeigenschappen aan te passen voor de api.
var apiOptions = {
    bezierCurve : false,
    animation: {
        onComplete: done
    }
}

// Roept de methode done niet aan omdat het dashboard het niet nodig heeft. Options is ook om grafiekeigenschappen aan te passen voor het dashboard.
var dashboardOptions = {
    bezierCurve : false,
}

// dataSet en options worden in de willMount geselecteerd afhankelijk van de props.
let dataSet;
let options;

export class Graph extends React.Component {
    // Voor het renderen word de dataset geselecteerd. Dataset is de parameter in de url.
    componentWillMount() {
        // apiOptions genereert een afbeelding, dashboardOptions doet dat niet.
        switch(this.props.goal) {
            case "dashboard":
                options = dashboardOptions;
                break;
            case "api":
                options = apiOptions;
                break;
            default:
                options = dashboardOptions;
        }
        // 'id' geeft de dataset aan. indien niet aangegeven of foutief word het d1.
        switch(this.props.id) {
            case "d1":
                dataSet = d1;
                break;
            case "d2":
                dataSet = ["TestSet"];
                break;
            default:
                dataSet = d1;
            }
        };

    render() {
        return(
            <div>
              <Line ref={ref => this.chart = ref}  options={options} data={dataSet} />
            </div>
        );

    }
};



