import React from 'react';
import { Graph } from '../components/Graph';


class ApiGraph extends React.Component {
    render() {
            return (
                <div>
                    <img id="graph"/>
                    <div style={{visibility: 'hidden'}}>
                        <Graph goal="api" id={this.props.match.params.graph} />
                    </div>
                </div>
            );
    };
}
export default ApiGraph;



