import React from 'react';
import { Graph } from '../components/Graph';

class ApiHome extends React.Component {
    render() {
        return (
            <div>
                <img id="graph"/>
                <div style={{visibility: 'hidden'}}>
                    <Graph />
                </div>
            </div>
        );
    }
}
export default ApiHome;



