import React from 'react';
import ReactDOM from 'react-dom';
import '../App.css';
import html2canvas from 'html2canvas';

import { Sidebar } from '../components/Sidebar.js';
import { Card } from '../components/Card.js';
import { Graph } from '../components/Graph.js';
import { Doughnuts } from '../components/Doughnuts.js';
import { BarChart } from '../components/BarChart.js';
import { Radars } from '../components/Radars.js';
import { Scatter } from '../components/Scatter.js';
import { PolarPie } from '../components/PolarPie.js';



class App extends React.Component {
  render() {
    return (
      <div>
          <Sidebar />
          <div  class="container">
                <div class="row">
                    <div class="col-lg-6">
                        <Card name="Aantal cola's per maand" targetLink="/dashboard#/detail" text={<Graph goal="dashboard" id="d1" />}/>
                    </div>
                    <div class="col-lg-6">
                        <Card name="Dynamisch herladen" targetLink="/dashboard#/detail" text={<Doughnuts />}/>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-12">
                        <Card name="Aantal KwH van USP Selficient" targetLink="/dashboard#/detail" text={<BarChart />}/>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-12">
                        <Card name="Het leven van een programmeur" targetLink="/dashboard#/detail" text={<Radars />}/>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-6">

                        <Card name="Scatter grafiek" targetLink="/dashboard#/detail" text={<Scatter />}/>
                    </div>
                    <div class="col-lg-6">
                        <Card name="Pie" targetLink="/dashboard#/detail" text={<PolarPie />}/>
                    </div>
                </div>
                <img id="url" />
            </div>
      </div>
    );
  }
}

export default App;



